import { configureStore } from '@reduxjs/toolkit';
import createSagaMiddleware from 'redux-saga';
import navReducer from '@store/slices/navSlice';
import projectsReducer from '@store/slices/projectsSlice';
import contactReducer from '@store/slices/contactSlice';
import rootSaga from '@store/sagas/rootSaga';
import { loggerMiddleware } from '@store/middleware';
import type { RootState } from '@/types';

// â”€â”€â”€ Create Saga Middleware â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€
const sagaMiddleware = createSagaMiddleware();

// â”€â”€â”€ Configure Store â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€
const store = configureStore({
  reducer: {
    nav: navReducer,
    projects: projectsReducer,
    contact: contactReducer,
  },
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({
      serializableCheck: false,
    }).concat(sagaMiddleware, loggerMiddleware),
});

// â”€â”€â”€ Run Root Saga â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€
sagaMiddleware.run(rootSaga);

// â”€â”€â”€ Typed Dispatch (for use in hooks) â”€â”€â”€â”€â”€â”€â”€
export type AppDispatch = typeof store.dispatch;

// â”€â”€â”€ Re-export RootState so hooks can use it â”€
export type { RootState };

export default store;